<template>
  <div>
    <h1>CREATE A POLL</h1>
     <form @submit.prevent="createPoll">
      <fieldset>
        <div class="form-group">
          <label for="title">WHAT YOU WANT TO SHARE?</label>
          <input
            required
            v-model="title"
            type="text"
            class="form-control"
            id="title"
            placeholder="Enter Poll Title"
          />
        </div>

         <div class="form-group">
          <label for="totalOptions">HOW MANY OPTIONS WILL BE?</label>
          <input
            required
            v-model.number="totalOptions"
            type="number"
            class="form-control"
            id="totalOptions"
          />
        </div>

        <div class="form-group" v-if="totalOptions">
          <label for="options">WHAT ARE THE OPTIONS?</label>
          <input
           v-for="option in totalOptions" :key="option"
            required
            v-model="options[option-1]"
            type="text"
            :placeholder="`Enter Option ${option}`"
            class="form-control mb-1"
          />
        </div>
        
        <button 
        :disabled="!totalOptions"
        type="submit" 
        class="btn btn-success btn-block">
          SHARE
        </button>
      </fieldset>
    </form>
  </div>
</template>

<script>
export default {
  name: 'poll',
    data() {
    return {
      title: '',
      totalOptions: 0,
      options: [],
    };
  },

  methods: {
      createPoll() {
        this.$store.dispatch('createPoll', {
            title: this.title,
            totalOptions: this.totalOptions,
            options: this.options,
        }).then(() => {
            Object.assign(this.$data, this.$options.data())
            this.$toasted.success("New Poll Created Successfully");
        }).catch(err => console.error(err));
      }
  },
};
</script>
