import axios from 'axios';

const state = {
    polls:[1]
};

const actions = {
    createPoll({ commit }, poll) {
        return new Promise((resolve, reject) => {
            commit('auth_request');
            axios
            .post('/polls', poll)
            .then((res) => {                
                resolve(res);
            })
            .catch((err) => {
                reject(err);
            });
        });
    },
};

const mutations = {

};

const getters = {

}

export default {
    state,
    getters,
    actions,
    mutations
}