<template>
  <div>
    <h1>
      POLLS [{{this.polls.length}}]
      <router-link class="float-right btn btn-small btn-success" to="/create-poll">
        Create A POLL
      </router-link>
    </h1>
    <div v-if="this.isLoading" class="d-flex justify-content-center">
      <PulseLoader />
    </div>
    <div v-else-if="this.polls.length">
        <div class="card mb-2" style="" v-for="(poll, key) in this.polls" :key="key">
          <div class="card-body">
              <h5 class="card-title">
                  <span class="font-weight-bold">{{poll.title}}</span>
                  <small class="float-right"> {{timeAgo(poll.createdAt)}}</small>
              </h5>
              <strong>Select From Following Options:</strong>
              <br>
              <div class="mt-4">
                  <Vote :votes="poll.votes" :pollId="poll._id" :pollOptions="poll.options"/>
              </div>
              <div class="mt-4">
                <Comments :pollId="poll._id" :comments="poll.comments" :timeAgo="timeAgo"/>
              </div>
          </div>
      </div>
    </div>
    <div v-else class="alert alert-danger">
        <strong>Polls, Not Found!!</strong>
    </div>
  </div>
</template>


<script>
import axios from 'axios';
import moment from 'moment';
import Comments from '@/components/Comments.vue';
import Vote from '@/components/Vote.vue';
import PulseLoader from 'vue-spinner/src/PulseLoader.vue'
export default {
  name: 'dashboard',
   components: {
    Comments,
    Vote,
    PulseLoader
  },
    data() {
    return {
      polls: [],
      isLoading: true,
    };
  },
    mounted() {
      this.fetchPolls() 
  },
  methods: {
        timeAgo(createdAt) {
          return moment(createdAt).fromNow();
        },

        fetchPolls () {
           axios
            .get('/polls',)
            .then((res) => {                
               this.polls = res.data.data
               this.isLoading = false;
            })
            .catch(() => {
               this.$toasted.error("Unable to fetch Polls!!");
            })
      }
  },
};
</script>
