<template>
  <div class="row" v-if="!this.isLoading">
    <div class="col-6">
            <div class="form-check mb-1" 
          style="border:1px solid black; padding:8px 30px 8px; border-radius: 10px"
              v-for="(option, index) in pollOptions" :key="index">
                <input class="form-check-input" 
                type="radio" 
                name="exampleRadios"
                v-model="selectedOption"
                :checked="selectedOption == index"
                :id="`${pollId}-${index}`" 
                :value="index">
                <label class="form-check-label" :for="`${pollId}-${index}`">
                    {{option}}
                </label>
            </div> 
            
            <button 
            :disabled="this.isLoading || this.isVoted"
            class="btn btn-success btn-small" @click="voteHandler">
                {{isVoted ? "Voted" : "Vote"}}
            </button>
    </div>
    <div class="col-6">
        <div class="small">
        <line-chart :width="200" :height="200" :chart-data="this.chartData" ></line-chart>
      </div>
    </div>
  </div>
  <div v-else>
      <PulseLoader />
  </div>
</template>

<script>
import axios from 'axios';
import LineChart from './LineChart'
import PulseLoader from 'vue-spinner/src/PulseLoader.vue'

export default {
  name: 'vote',
  props: ['votes', 'pollId', 'pollOptions'],
  components: {
      LineChart,
      PulseLoader
  },

  data() {
    return {
      isVoted: false,
      isLoading: true,
      chartData:  null,
      selectedOption: null,
      user: JSON.parse(this.$store.getters.user),
    };
  },

  mounted() {
    this.getVotes();
  },
  methods: {

    voteHandler() {
        const vote = {
          pollId: this.pollId,
          selectedOption: this.selectedOption  
        }
        axios
        .post(`/polls/vote`, vote)
        .then(() => {                
            this.getVotes();
            this.isLoading = false;
        })
        .catch(() => {
            this.$toasted.error("Unable to vote!!");
            this.isLoading = false;
        })
    },

    getVotes () {
        axios
        .get(`/polls/get-votes/${this.pollId}`)
        .then((res) => {   
          const { selectedOption, data } = res.data
          this.chartData  = data
          this.selectedOption = selectedOption
          this.isVoted  = selectedOption !== null
          this.isLoading = false;
        })
        .catch(() => {
            this.$toasted.error("Unable to fetch votes!!");
            this.isLoading = false;
        })
    }
  },
};
</script>
<style>
  .small {
    max-width: 200px;
    float: right;
  }
</style>