<template>
  <div class="mt-2">
    <span class="font-weight-bold">Comments ({{this.comments.length}})</span>
    <form @submit.prevent="submitComment">
        <div class="form-group">
            <div class="form-group">
                <div class="input-group mb-3">
                <input type="text" 
                class="form-control" 
                v-model.trim="newComment"
                placeholder="Write comments...">
                <div class="input-group-append">
                    <button 
                    type="submit"
                    :disabled="!this.newComment.length"
                    class="btn btn-success btn-small">
                        Submit
                    </button>
                </div>
                </div>
            </div>
        </div>
    </form>
    <ul class="list-group" v-if="this.comments.length">
        <li class="list-group-item d-flex justify-content-between align-items-center"
        v-for="(comment, index) in this.comments" :key="index"
        >
            {{comment.text}}
            <span>
               <small>{{comment.by}}({{timeAgo(comment.createdAt)}})</small>
            </span>
        </li>
    </ul>
  </div>
</template>

<script>
import axios from 'axios'
export default {
   name: 'comments',
   props: ['pollId', 'comments', 'timeAgo'],
   data() {
       return {
           newComment: ''
       }
   },
   methods: {
       submitComment () {
           axios.post(`/polls/comments/${this.pollId}`, {
               text: this.newComment,
            }).then((res) => { 
                this.comments = res.data.data.comments.reverse();
                this.$toasted.success("Comment posted successfully!!");
                this.newComment = ''               
            })
            .catch((error) => {
               this.$toasted.error(error.response.data.message || "Unable to post comment!!");
            });
       }
   },
};
</script>
